@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro:regular,bold,italic&subset=latin,latin-ext);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

header {
  background-color: #eee;
  font-family: Helvetica, sans-serif;
  font-weight: 300;
  font-size: 16px;
  line-height: 1.7;
}

.profile-card {
  text-align: center;
}

.profile-picture {
  border-radius: 100%;
  width: 150px;
  height: 150px;
}

.title {
  font-weight: normal;
  font-size: 20px;
  margin: 5px;
}

.about {
  margin-top: 40px;
}

.grid {
  display: flex;
  flex-wrap: wrap;
}

body {
  background-color: #eee;
}

a {
  color: #4F90CD;
  text-decoration: none;
  font-weight: normal;
}

p {
  margin-top: 20px;
  margin-bottom: 20px;
}

.container-small {
  max-width: 700px;
  margin: 0px auto;
  padding: 40px 20px;
}

.container-medium {
  max-width: 1500px;
  margin: 0px auto;
  padding: 40px 20px;
}

